                        <p>–í—Ö–æ–¥—è—á–∏ –≤ –∞–∫–∞—É–Ω—Ç, –≤—ã –ø–æ–≥–æ–¥–∂—É—î—Ç–µ—Å—å –∑ <a href="/">–∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–æ—é —É–≥–æ–¥–æ—é</a> —ñ,
                            –ø—ñ–¥—Ç–≤–µ—Ä–¥–∂—É—î—Ç–µ,
                            —â–æ –æ–∑–Ω–∞–π–æ–º–∏–ª–∏—Å—å –∑ <a href="/">–ø–æ–ª—ñ—Ç–∏–∫–æ—é –∫–æ–Ω—Ñ—ñ–¥–µ–Ω—Ü—ñ–π–Ω–æ—Å—Ç—ñ</a>.</p>
