import React, {Component} from 'react';

import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {ROUTES} from '../../../../Routes/routes';
import {userActions} from '../../../../Actions';

import '../login.css';
import '../forms.css';

import 'bootstrap-social';

import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faTimesCircle} from '@fortawesome/free-regular-svg-icons';
import {MDBBtn, MDBCol, MDBContainer, MDBInput, MDBRow} from 'mdbreact';
import Logo from '../../../../assets/img/MIIIIIIIIIIII.jpg';

class SignInPage extends Component {
	constructor(props) {
		super(props);

		this.state = {
			email: '',
			password: '',
			submitted: false,
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(e) {
		const {name, value} = e.target;
		this.setState({[name]: value});
	}

	handleSubmit(e) {
		e.preventDefault();

		this.setState({submitted: true});
		const {email, password} = this.state;
		if (email && password) {
			this.props.login(email, password);
		}
	}

	render() {
		const {loggingIn} = this.props;
		const {email, password, submitted} = this.state;
		return (
			<div className="signIn-content">
				<div className="signIn-header">
					<div className="signIn-logo">
						<Link to="/">
							<img style={{paddingTop: '8px'}} src={Logo} alt="logo" width={200} height={75} />
						</Link>
					</div>
					<Link className="x-class" aria-label="back to HR's" to={ROUTES.MAIN}>
						<FontAwesomeIcon icon={faTimesCircle} size="2x" />
					</Link>
				</div>
				<div className="signIn-main">
					<h1 className="signIn-title">–í—Ö—ñ–¥</h1>
					<div className="new-user">
						<h3>
							–£ –≤–∞—Å —â–µ –Ω–µ–º–∞—î –∞–∫–∞—É–Ω—Ç—É? <Link to={ROUTES.REGISTRATION}>–°—Ç–≤–æ—Ä–∏—Ç–∏</Link>
						</h3>
					</div>
					<div className="login-content">
						<MDBContainer>
							<MDBRow>
								<MDBCol>
									<form className="common-form" name="form" onSubmit={this.handleSubmit}>
										<div className={submitted && !email ? ' has-error' : ''}>
											<MDBInput
												name="email"
												label="–≠–ª. –ø–æ—à—Ç–∞"
												icon="envelope"
												group
												type="email"
												validate
												error="wrong"
												value={email}
												onChange={this.handleChange}
												success="right"
											/>
											{submitted && !email && (
												<div className="help-block">* –¶–µ –æ–±–æ–≤'—è–∑–∫–æ–≤–µ –ø–æ–ª–µ</div>
											)}
										</div>
										<div className={submitted && !password ? ' has-error' : ''}>
											<MDBInput
												name="password"
												label="–ü–∞—Ä–æ–ª—å"
												icon="lock"
												group
												type="password"
												validate
												value={password}
												onChange={this.handleChange}
											/>
											{submitted && !password && (
												<div className="help-block">* –¶–µ –æ–±–æ–≤'—è–∑–∫–æ–≤–µ –ø–æ–ª–µ</div>
											)}
										</div>
										<div className="form-check my-4">
											<MDBInput label="–ó–∞–ø–∞–º'—è—Ç–∞—Ç–∏ –º–µ–Ω–µ" type="checkbox" id="rememberMe" />
										</div>
										<div className="btn-group">
											<MDBBtn type="submit" className="form-btn" color="0">
												–£–≤—ñ–π—Ç–∏
											</MDBBtn>
											{loggingIn && (
												<img
													src="data:image/gif;base64,"
													alt="loading"
												/>
											)}
										</div>
									</form>
								</MDBCol>
							</MDBRow>
						</MDBContainer>
					</div>
					<div className="terms-of-use">
						<p>
							–í—Ö–æ–¥—è—á–∏ –≤ –∞–∫–∞—É–Ω—Ç, –≤—ã –ø–æ–≥–æ–¥–∂—É—î—Ç–µ—Å—å –∑ <a href="/">–∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–æ—é —É–≥–æ–¥–æ—é</a> —ñ,
							–ø—ñ–¥—Ç–≤–µ—Ä–¥–∂—É—î—Ç–µ, —â–æ –æ–∑–Ω–∞–π–æ–º–∏–ª–∏—Å—å –∑ <a href="/">–ø–æ–ª—ñ—Ç–∏–∫–æ—é –∫–æ–Ω—Ñ—ñ–¥–µ–Ω—Ü—ñ–π–Ω–æ—Å—Ç—ñ</a>.
						</p>
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	const {loggingIn} = state.auth;
	return {loggingIn};
};

const mapDispatchToProps = {
	login: userActions.login,
	logout: userActions.logout,
};

export default connect(mapStateToProps, mapDispatchToProps)(SignInPage);
