import React, {Component} from 'react';
import {connect} from "react-redux";
import {MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter, MDBIcon} from 'mdbreact';
import {userActions} from "../../../../../Actions";

class DeleteProfileModal extends Component {
    state = {
        modal: false
    };

    toggle = () => {
        this.setState({
            modal: !this.state.modal
        });
    };

    delete = () => {
        this.props.deleteProfile(this.props.userId);
        this.toggle();
    };

    render() {
        return (
            <div className="profile-save-data">
                <button className="profile-btn delete-btn" onClick={this.toggle}>–£–¥–∞–ª–∏—Ç—å</button>
                <MDBModal isOpen={this.state.modal} toggle={this.toggle}>
                    <MDBModalHeader toggle={this.toggle}>–£–¥–∞–ª–µ–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è</MDBModalHeader>
                    <MDBModalBody>
                        –í–∏ –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å?
                        <MDBIcon far icon="frown" style={{marginLeft: '10px'}}/> <br/>
                        –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –ø—Ä–∏–≤–µ–¥–µ—Ç –∫ –Ω–µ–æ–±—Ä–∞—Ç–∏–º–æ–º—É —É–¥–∞–ª–µ–Ω–∏—é –≤–∞—à–µ–≥–æ –∞–∫–∫–∞—É–Ω—Ç–∞.
                    </MDBModalBody>
                    <MDBModalFooter>
                        <MDBBtn className="modal-delete-btn" onClick={this.delete}>–£–¥–∞–ª–∏—Ç—å</MDBBtn>
                        <MDBBtn className="modal-close-btn" onClick={this.toggle}>–ó–∞–∫—Ä—ã—Ç—å</MDBBtn>
                    </MDBModalFooter>
                </MDBModal>
            </div>
        );
    }
}

const mapStateToProps = ({users}) => {
    return{
        loading: users.loading,
        deleted: users.deleted,
        error: users.error
    }
};

const mapDispatchToProps = {
    deleteProfile: userActions.deleteProfileData
};

export default connect(mapStateToProps, mapDispatchToProps)(DeleteProfileModal);
